import React from "react";
import PropTypes from 'prop-types';
import SourceStore from '../stores/SourceStore';
import { getSources } from '../actions/SourcesActions';
import Articles from './Articles'
import { Link } from "react-router";

import { getArticles } from '../actions/ArticlesActions';

export default class Welcome extends React.Component {
  constructor() {
    super();
    this.state = {
      sources: SourceStore.getAll(),
      search: ''
    };
    this.getSources = this.getSources.bind(this);
  }

  componentWillMount() {
    // SourceStore.on(this.getSources);

  }

  componentDidMount() {
    getSources();
    SourceStore.addChangeListener(this.getSources);
  }

  componentWillUnMount() {
    SourceStore.removeChangeListener(this.getSources);
  }

  getSources() {
    this.setState({
      sources: SourceStore.getAll(),
    });
    //console.log(SourceStore.getAll());
  }

  handleChange(event) {
    //const searchedSources = e.target.value;
    //this.changeSources(this.state.sources);
    //console.log(event);
    this.setState({ search: event.target.value })
    //console.log(this.state.search, "search");
  }

  render() {
    const { sources } = this.state;
    console.log(this.state, "sources");

    const filteredSources = this.state.sources.filter(source => source.name.toLowerCase().indexOf(this.state.search.toLowerCase()) !== -1);

    return (
      <div>
        <br/><br/>
        <h3>News from over 70 sources</h3>
        <div className= "col-md=8">
          <div>
        <input className="searchbox col col-lg-12" type="text" class="form-control" value={this.state.search} onChange={this.handleChange.bind(this)} />
        </div>
          <br/><br/>
        <div></div>
        <div className="card-columns ">
        <div className ="row">
          {filteredSources.map(source => {
            <Col xs="6" sm="4" className="tile" key={source.id}>
              <Card
                block
                className="bl" inverse color="info"
              >
                <CardTitle>{source.title}</CardTitle>

                <CardText className="desc">{source.description}</CardText>
                <div className="float-left">
                <CardText className="category">{source.category}</CardText></div>
                <div className="float-right">
                 <Button className="Read" 
                 onClick={this.handleQueryValue.bind(this, `${source.href}/${source.sortBysAvailable}`)}
                 >Read</Button>
                 </div>
              </Card>
            </Col>
          })}
        </div>
        </div>
        </div>
        <div></div>
      </div>

    )
  }
};





